.contactPage {
    .contactPage__row {
        display: flex;
        margin-top: 4rem;
    }

    .contactPage__col {

    }

    .contactPage__col--info {
        width: 40%;
    }

    .contactPage__col--map {
        width: 60%;
    }

    .contactPage__companyName {
        display: block;
        font-size: 16px;
        color: #000;
        margin-bottom: 0.5rem;
    }

    .contactPage__companyInfo {
        display: flex;
        align-items: flex-start;
    }

    .contactPage__icon {
        margin-right: 0.5rem;
        width: 40px;
        height: 40px;
    }

    .contactPage__contacts {
        display: flex;
        align-items: flex-start;

        .contactPage__contact {
            display: block;
        }
    }
    

    .contactPage__map {
        width: 100%;
        height: 450px;
    }
}